import React from 'react';
import styled from 'styled-components';

import pkg from '../../package.json';
import logo from 'images/logo.png';

const StyledLogo = styled.img.attrs({ src: logo, alt: pkg.name })`
  width: 100%;
  display: block;
  max-width: 640px;
`;

const Logo = () => <StyledLogo />;

export default Logo;
